html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: #FFF;
}
body .container {
  max-width: 1440px;
  margin: 0 auto;
  min-height: 100vh;
}
body .container .overlay {
  background-color: #121212;
  opacity: 0.5;
  display: none;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.main {
  display: flex;
}
.main_image {
  width: 505px;
  height: 330px;
  padding: 95px 47.5px 95px 230px;
}
.main_content {
  padding: 135px 230px 135px 47.5px;
  color: #121212;
  font-family: "Roboto", sans-serif;
}
.main_content_title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
}
.main_content_paragraph {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  margin-top: 24px;
}
.main_content_button {
  padding: 16px 40px;
  background: #E4C1AE;
  border-radius: 32px;
  border: none;
  color: #FFF;
  outline: none;
  margin-top: 50px;
  transition: transform 0.3s cubic-bezier(1, 0.21, 0.4, 1.26);
}
.main_content_button:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.modal {
  display: none;
  position: fixed;
  top: 50%;
  width: 920px;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "Roboto", sans-serif;
  color: #121212;
  background-color: #FFF;
}
.modal_title {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  width: fit-content;
  margin: 56px 0 0 95px;
}
.modal_paragraph {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  width: fit-content;
  margin: 24px 0 56px 95px;
}
.modal_close {
  position: absolute;
  right: 46px;
  top: 46px;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 36px;
  font-weight: 700;
  padding: 0;
  line-height: 12px;
}
.modal_close:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.show {
  display: block;
}